
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {donation} from '../config'
export default {
  name: 'donation',
  head: {
    title: {
      inner: '积分转赠'
    }
  },
  components: {
    Group,
    Cell,
    XInput,
    XButton
  },
  data () {
    return {
      height: '',
      balance: 0,
      hadPayPwd: false,
      form: {
        userId: '',
        score: '',
        target: '',
        payPwd: ''
      }
    }
  },
  created () {
    this.balance = this.$localStorage.get('balance')
    this.form.userId = JSON.parse(this.$localStorage.get('userInfo')).userId
    this.hadPayPwd = JSON.parse(this.$localStorage.get('userInfo')).hadPayPwd
  },
  mounted () {
    this.height = document.querySelector('.content').clientHeight + 'px'
  },
  methods: {
    handleSubmit () {
      if (JSON.parse(this.$localStorage.get('userInfo')).hadPayPwd === false) {
        this.$vux.toast.show({
          type: 'text',
          width: '115em',
          position: 'bottom',
          text: '您还未设置支付密码，请前往设置！',
          time: '1000'
        })
      } else if (!this.form.target) {
        this.$vux.toast.show({
          type: 'text',
          width: '15em',
          position: 'bottom',
          text: '请填写获赠用户！',
          time: '1000'
        })
      } else if (this.form.target === JSON.parse(this.$localStorage.get('userInfo')).userTel) {
        this.$vux.toast.show({
          type: 'text',
          width: '15em',
          position: 'bottom',
          text: '转赠对象不能是自己！',
          time: '1000'
        })
      } else if (this.form.score <= 0 || !this.form.score) {
        this.$vux.toast.show({
          type: 'text',
          width: '15em',
          position: 'bottom',
          text: '赠予积分必须大于0！',
          time: '1000'
        })
      } else if (!this.form.payPwd) {
        this.$vux.toast.show({
          type: 'text',
          width: '15em',
          position: 'bottom',
          text: '支付密码不得为空！',
          time: '1000'
        })
      } else if (this.form.score > parseInt(this.balance)) {
        this.$vux.toast.show({
          type: 'text',
          width: '17em',
          position: 'bottom',
          text: '赠与的积分不得大于现有积分！',
          time: '1000'
        })
      } else {
        this.$http({
          method: 'jsonp',
          url: donation,
          jsonp: 'callback',
          jsonpCallback: 'json',
          params: this.form,
          before: () => {
          }
        })
        .then(res => {
          let balance = this.balance - this.form.score
          const userInfo = JSON.parse(this.$localStorage.get('userInfo'))
          userInfo.userBalance = balance
          this.$localStorage.set('userInfo', JSON.stringify(userInfo))
          if (res.body.status) {
            this.$vux.toast.show({
              type: 'text',
              width: '20em',
              position: 'bottom',
              text: '转赠成功！',
              time: '1000'
            })
            setTimeout(() => {
              this.$router.replace('/wallet/' + this.form.userId)
            }, 1000)
          } else {
            this.$vux.toast.show({
              type: 'text',
              width: '20em',
              position: 'bottom',
              text: res.body.msg,
              time: '1000'
            })
          }
        })
      }
    }
  }
}



